<template>
    <div>
        <!-- <div class="card">
            <div class="card-content" v-for= "(item,i) in part.object.source" :key ="i">
                <div> {{ item.title}}</div>
            </div>
        </div> -->
        <div class="card">
         <div class="card-content" v-for = "(story, i) in part.object.source" :key="i">
            <div class="card-definition">
                <div class="card-title" ><h2>{{story.title}}</h2></div>
                <p>{{story.content}}</p>
            </div>
        </div> 
    </div>  
    </div>
</template>
<script>

export default {
    name: 'dummy',
    props: ['education', 'disaster','environment','women'],
    computed: {
            part() {
                
                if(this.disaster){
                    return{
                        object: this.disaster
                    }
                }
                if(this.education){
                    return{
                        object:  this.education
                    }
                }
                if(this.environment){
                    return{
                        object: this.environment
                    }
                }
                if(this.women){
                    return {
                        object:  this.women
                    }
                } 
        }  
    }
};   
</script>

<style scoped>
.card{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}
.card-content{
    border-style: solid;
    border-width: 1px;
