<template>
    <div class ="gallery-section">
          <h1 class="display-4" style="text-align: center;margin-bottom: 40px;margin-top:100px"> Gallery</h1>
          <div class="grid" @mouseover="myfunc()" @mouseout="myfunction()" >
            
            <div class="item"  v-for= "(image, i) in availableImage.source " :key="i" 
            :style= "{ 'background-image': 'url(' + image.src + ')' }">
            </div>
             
          </div>
    </div>
</template>

<script>
import availableImage from '../data/gallery/image';
export default {
    name: 'Gallery',
    data() {
      return {
        availableImage,
      };
    },
  methods: {
        myfunc() {
            console.log("hello");
            var a = document.getElementsByClassName('item');
            var i;
            for (i = 0; i < a.length; i++) 
                a[i].className += " swirl-in-fwd";

        },
        myfunction() {
            var a = document.getElementsByClassName('item');
            var i;
            for (i = 0; i < a.length; i++) 
                a[i].className = a[i].className.replace(/\bswirl-in-fwd\b/g, "").trim();
        }
  }
    }
</script>
<style scoped>
  .display-4{
    font-size: 3.5rem;
    font-weight: 300;
    line-height: 1.2;
    display: block;
    font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    color: green;
  }
  .grid {
    display: grid;
    grid-template-rows: 
      repeat(4, 180px);
    grid-template-columns: 
      repeat(3, 1fr);
    grid-gap: 5px;
  }
  
  .item:nth-child(1) {
    grid-row: 1 / 4;
  }
  
  .item:nth-child(2) {
    grid-column: 2 / 4;
    grid-row: 1 / 3;
  }
  
  .item:nth-child(3) {
    grid-column: 3 / 4;
    grid-row: 3 / 5;
  }
  
  .item:nth-child(4) {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
  }
  
  .item:nth-child(5) {
    grid-column: 1 / 3;
    grid-row: 4 / 5;
  }
  
  /* styling code */
  
  .grid {
    margin:20px auto;
    max-width:1000px;
  }
  
  .grid > .item {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
.swirl-in-fwd {
    -webkit-animation: swirl-in-fwd 2s ease-out both;
	animation: swirl-in-fwd 2s ease-out both;
}
@-webkit-keyframes swirl-in-fwd {
    0% {
      -webkit-transform: rotate(-540deg) scale(0);
              transform: rotate(-540deg) scale(0);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotate(0) scale(1);
              transform: rotate(0) scale(1);
      opacity: 1;
    }
}
@keyframes swirl-in-fwd {
    0% {
      -webkit-transform: rotate(-540deg) scale(0);
              transform: rotate(-540deg) scale(0);
      opacity: 0;
    }
    100% {
      -webkit-transform: rotate(0) scale(1);
              transform: rotate(0) scale(1);
      opacity: 1;
    }
}
</style>